/* Блок __feature (.advantages__feature) */

/* Блок __list (.advantages__list) */
.advantages__list,
.clients__list {
  margin: 0 auto;
  align-content: center;
  gap: 30px;
  list-style: none;
  /* grid-template-columns: repeat(4, 1fr);*/
}

.advantages__feature {
  margin: 0 auto;
  width: 100%;
  padding-bottom: 40px;
}
/* Блок __group (.advantages__group) */
.advantages__group {
  max-width: 450px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 120px;
  padding-top: 25px;
  padding-bottom: 25px;
  background-color: #f5f4fa;
}
/* Блок __icon (.advantages__icon) */
.advantages__icon {
  width: 70px;
  height: 70px;
}
.advantages__title-grup {
  text-align: center;
  /* Заглушка для переменной $colorTxt-secondary. */
  color: #212121;
  /* Часто используется для вторичного текста */
  /* Заглушка для fontSize().text-align: center; */
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  text-transform: uppercase;
  padding-top: 30px;
  padding-bottom: 10px;
}
/* Блок __text (.advantages__text) */
.advantages__text {
  /*display: inline-block;
  /Заглушка для fontSize(). 
  font-size: 14px;*/
  font-weight: 400;
  margin-bottom: 30px;
  text-align: left;

}

/*
 * Стили для основного блока .clients
 */
/*
 * ------------------------------------------------------------------
 * ОСНОВНЫЕ СТИЛИ (MOBILE FIRST - 2 элемента в ряд)
 * ------------------------------------------------------------------
 */
.clients {
  text-align: center;
}

.clients__title {
  font-weight: 700;
  font-size: 28px;
  line-height: 1.18;
  padding-bottom: 30px;
  /* Цвет заголовка: #212121 */
  color: #212121;
}

/* 📱 МОБИЛЬНЫЙ (По умолчанию): 2 клиента в ряд */
.clients__list {
  display: grid;
  /* Промежуток (gap): 30px */
  gap: 30px;
  grid-template-columns: repeat(2, 1fr);
}

.clients__item {
  display: flex;
  height: 92px;
  justify-content: center;
  align-items: center;

  /* Цвет ссылок/бордера: #AFB1B8 */
  border: 1px solid #afb1b8;
  border-radius: 4px;
  fill: #afb1b8;

  margin-bottom: 0;
  transition: fill 500ms ease-in-out;
}

.clients__link {
  display: flex;
  padding: 0;
  margin: 0;
  fill: inherit;
}

.clients__icon {
  width: 106px;
  height: 40px;
  fill: inherit;
}
/*
 * ------------------------------------------------------------------
 * СТИЛИ ХОВЕРА
 * ------------------------------------------------------------------
 */
.clients__item:hover,
.clients__item:focus {
  /* Акцентный цвет: #2196F3 */
  border: 1px solid #2196f3;
  fill: #2196f3;
  cursor: pointer;
  transition: fill 250ms ease-in-out, border-color 250ms ease-in-out;
}

@media (min-width: 480px) {/*
  .advantages__list {
    display: flex;
    flex-wrap: wrap;
  }*/

  .advantages__title-grup {
    text-align: start;
  }
}
@media (min-width: 768px) {
  .advantages__list {
    /*justify-content: space-between;*/
    display: grid;
    /*grid-auto-flow: dense;*/
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 2rem;
  }
  .clients__list {
    grid-template-columns: repeat(3, 1fr);
  }
    .advantages__feature {/*
    align-content: stretch;*/
    padding-bottom: 0px;
  }
}

/* Медиа-запрос для .advantages__feature (min-width: 1200px) */
@media (min-width: 1280px) {
  .advantages {
    margin-top: 93px;
    margin-bottom: 0;
  }

  .advantages__list {
    /*justify-content: space-between;*/
    grid-template-columns: repeat(4, 1fr);
  }

  .clients__title {
    padding-bottom: 50px;
    font-size: 36px;
  }

  .clients__list {
    grid-template-columns: repeat(6, 1fr);
  }
}

