/* s.module.css - Mobile First Approach */

/* ======================================= */
/* 1. БАЗОВЫЕ СТИЛИ (MOBILE / ДО 768px)  */
/* ======================================= */

.gallery {
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    margin: 0;
    gap: 30px;
}

.item {
    /* 1 элемент в ряд (Мобильный) */
    flex-basis: 100%;
    position: relative;
    overflow: hidden;
    transition: box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.item img {
    display: block;
    width: 100%;
    height: auto;
}

/* Контейнер подписи (s.bgTitle) - ВСЕГДА ВИДИМ на маленьких экранах */
.bgTitle {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: auto;
    /* Адаптивная высота */
    padding: 15px;
    /* Уменьшенные отступы для мобильных */

    background-color: rgba(47, 48, 58, 0.8);
    display: flex;
    justify-content: center;
    align-items: center;

    /* Обязательно: на мобильных opacity: 1 по умолчанию! */
    opacity: 1;
}

.imgTitle {
    color: #ffffff;
    font-weight: 700;
    font-size: 16px;
    /* Чуть меньше для мобильных */
    line-height: 1.17;
    text-align: center;
    text-transform: uppercase;
    margin: 0;
}

