}*/

/* ======================================= */
/* 2. ПЛАНШЕТЫ (768px до 1199px) - СЛАЙС  */
/* ======================================= */
@media screen and (min-width: 768px) and (max-width: 1199px) {

    /* 1. Контейнер галереи (ul.gallery) */
    .gallery {
        /* ГЛАВНОЕ: ОТКЛЮЧАЕМ перенос строк */
        flex-wrap: nowrap;

        /* ГЛАВНОЕ: ДОБАВЛЯЕМ горизонтальную прокрутку */
        overflow-x: auto;
        overflow-y: hidden;
    }

    /* 2. Элемент списка (li.item) */
    .item {
        /* Делаем элементы НЕ сжимаемыми и НЕ растяжимыми */
        flex-shrink: 0;
        flex-grow: 0;

        /* Ширина для 2х элементов в ряду (должно умещаться 2 элемента + немного места для следующего) */
        flex-basis: calc((100% - 30px) / 2);
    }

    /* Стили для подписи (На планшетах она видима по умолчанию) */
    .bgTitle {
        opacity: 1;
        /* Сохраняем видимость */
    }

    .imgTitle {
        font-size: 18px;
    }

    /* Убеждаемся, что Hover-эффекты не активны */
    .item:hover .bgTitle {
        opacity: 1;
    }

    .item:hover {
        box-shadow: none;
    }
}

/* ======================================= */
/* 3. ДЕСКТОП (1200px и выше)           */
/* ======================================= */
@media screen and (min-width: 1200px) {
    .item {
        /* 3 элемента в ряд */
        flex-basis: calc((100% - 60px) / 3);
    }

    /* СКРЫВАНИЕ ПОДПИСИ ПО УМОЛЧАНИЮ (Только на десктопе!) */
    .bgTitle {
        opacity: 0;
        height: 70px;
        /* Фиксированная высота для десктопа */
        padding: 24px;
        /* Восстанавливаем отступы */
        transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
    }

    /* Активация HOVER-эффекта (Только на десктопе!) */
    .item:hover .bgTitle {
        opacity: 1;
        /* Показывает подпись при наведении */
    }

    .item:hover {
        box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.12),
            0px 4px 4px rgba(0, 0, 0, 0.06),
            1px 4px 6px rgba(0, 0, 0, 0.16);
    }
}